@import "./_variables";
@import "../utils/_functions";

@mixin format-base {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 20px;
  font: inherit;
  vertical-align: baseline;
}

@mixin border($color) {
  border: 2px solid $color;
}

@mixin border-bottom {
  border-bottom: $border-caption;
  bottom: 0;
}

@mixin border-left {
  border-left: $border-caption;
  left: 0;
}

@mixin border-top {
  border-top: $border-caption;
  top: 0;
}

@mixin border-right {
  border-right: $border-caption;
  right: 0;
}

@mixin caption {
  content: $content-empty;
  position: absolute;
  height: 20px;
  width: 20px;
  @include transition(all, 1s, ease-in-out);
}

@mixin corners($top, $right, $bottom, $left) {
  top: $top;
  right: $right;
  bottom: $bottom;
  left: $left;
}

@mixin display($display, $width) {
  display: $display;
  width: $width;
}

@mixin header {
  position: relative;
  text-transform: uppercase;
  font-size: 22px;
}

@mixin line-horizontal {
  position: absolute;
  content: $content-empty;
}

@mixin margins($top-bot, $right-left) {
  margin: $top-bot $right-left;
}

@mixin padding($top-bot, $right-left) {
  padding: $top-bot $right-left;
}

@mixin size($width, $height) {
  width: $width;
  height: $height;
}

@mixin sizePx($width, $height) {
  width: ($width / 16) + remss;
  height: ($height / 16) + rem;;
}

@mixin trans-centered {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

@mixin transform($sx, $sy) {
  -webkit-transform: scale($sx, $sy);
  -ms-transform: scale($sx, $sy);
  -o-transform: scale($sx, $sy);
  transform: scale($sx, $sy);
}

@mixin transform-origin($x, $y) {
  -webkit-transform-origin: $x $y;
  -ms-transform-origin: $x $y;
  -o-transform-origin: $x $y;
  transform-origin: $x $y;
}

@mixin transition($elem, $time, $func) {
  -webkit-transition: $elem $time $func;
  -o-transition: $elem $time $func;
  transition: $elem $time $func;
}
